import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home.vue'
import Noticias from '../views/Noticias.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        auth: true,
        title: 'Centro de Terapia del Comportamiento',
        metaTags: [
          {
            name: 'description',
            content: 'The home page of our example app.'
          },
          {
            property: 'og:description',
            content: 'The home page of our example app.'
          }
        ]
      }
    },
    {
      path: '/noticias',
      name: 'noticias',
      component: Noticias,
      meta: {
        auth: true,
        title: 'Noticias | Centro de Terapia del Comportamiento',
        metaTags: [
          {
            name: 'description',
            content: 'The home page of our example app.'
          },
          {
            property: 'og:description',
            content: 'The home page of our example app.'
          }
        ]
      }
    },
  ],
})
