import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    articles: require('@/data/articles.json'),
    sucursales: require('@/data/sucursales.json'),
    drawer_items: require('@/data/drawer_items.json'),
    comunas: require('@/data/comunas.json'),
    ciudades: require('@/data/ciudades.json'),
    especialidades: require('@/data/especialidades.json'),
    testimonios: require('@/data/testimonios.json'),
    noticias: require('@/data/noticias.json'),
    drawer: false,
    
    sliders: [
     
        {
          src: require('@/assets/img/sliders/slide1.jpg'),
        },
        {
          src: require('@/assets/img/sliders/slide2.jpg'),
        },
        {
          src: require('@/assets/img/sliders/slide3.jpg'),
        },
       
    ]
  },
  getters: {
    categories: state => {
      const categories = []

      for (const article of state.articles) {
        if (
          !article.category ||
          categories.find(category => category.text === article.category)
        ) continue //pasa a la siguiente iteracion

        const text = article.category

        categories.push({
          text,
          href: '#!',
        })
      }

      return categories.sort().slice(0, 4)
    },
    links: (state, getters) => {
      return state.items.concat(getters.categories)
    },
  },
  mutations: {
    setDrawer: (state, payload) => (state.drawer = payload),
    toggleDrawer: state => (state.drawer = !state.drawer),
  },
  actions: {

  },
})
